export default {
	data() {
		return {
			timer: null,
			counter: 3
		}
	},
	methods: {
		stopCounter() {
			clearInterval(this.timer)
			uni.reLaunch({
				url: '/components/content/login/login'
			})
		}
	},
	created() {
		this.timer = setInterval(() => {
			if(this.counter <= 0) {
				clearInterval(this.timer)
			}else {
				this.counter -= 1
			}
			
		}, 1000)
	}
}
